import PropTypes from 'prop-types';
import useMediaQuery from '@mui/material/useMediaQuery';
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
import { Typography, Box, Link } from '@mui/material';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import commonUtils from 'utils';
import ProfileLink from 'components/profile';
import { Branding, BrandLogo } from 'components/brand';

import LoginButton from 'components/LoginButton';

export default function Header({ headerRef, name, handlePreLogin }) {
  console.log('in collage header', name);
  const cookieValue = commonUtils.getUserInfo();
  const isLoggedIn = cookieValue !== '';
  const belowmd = useMediaQuery((theme) => theme.breakpoints.down('md'));
  return (
    <Paper
      ref={headerRef}
      sx={{
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        p: 1,
        backgroundColor: 'primary.main',
        borderRadius: 0,
      }}
      elevation={0}
    >
      <Box sx={{ postion: 'absolute', left: '5px' }}>
        <Stack direction="row" alignItems="center" spacing={2}>
          <Link href="/collages" color="white" underline="none">
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              <ArrowBackIcon fontSize="medium" />
            </Box>
          </Link>
          {belowmd ? (
            <BrandLogo width="50px" height="50px" />
          ) : (
            <Branding width="50px" height="50px" />
          )}
        </Stack>
      </Box>
      <Typography
        variant="h4"
        component="h4"
        sx={{
          color: 'gray',
          position: 'absolute',
          left: '50%',
          transform: 'translate(-50%, 0)',
        }}
      >
        {name}
      </Typography>

      {isLoggedIn === true ? (
        <ProfileLink />
      ) : (
        <LoginButton
          authURL={process.env.REACT_APP_AUTH_URL}
          next="collage/create"
          handlePreLogin={handlePreLogin}
        />
      )}
    </Paper>
  );
}

Header.defaultProps = {
  handlePreLogin: () => {},
};

Header.propTypes = {
  name: PropTypes.string.isRequired,
  headerRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({ current: PropTypes.instanceOf(Element) }),
  ]).isRequired,
  handlePreLogin: PropTypes.func,
};
